import React from "react";
import fundadora from "../images/Fundadora.png";
import { Typography, Grid, Box } from "@mui/material";

export default function ComoFunciona() {
  return (
    <section
      style={{
        backgroundColor: "#fbf1e4",
        display: "flex",
        alignItems: "center",
        height: "88.1vh",
        padding: "2rem"
      }}
    >
      <Grid container>
        <Grid item xs={12} md={5}>
          <img
            src={fundadora}
            alt=""
            style={{
              maxWidth: "100%", height: "auto" ,
            }}
          />
        </Grid>
        <Grid
          item
          xs={12} md={7}
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Typography
            variant="h2"
            color="#2e5137"
            style={{
              paddingBottom: "3rem",
              fontFamily: "Next Southerland Serif DEMO",
            }}
          >
            Nosotras
            <br />
          </Typography>
          <Typography
            variant="body1"
            color="#2e5137"
            style={{ width: "30rem" }}
          >
            Nuestra empresa se fund a raz de un viaje que nuestra fundadora,
            Marina Herrn, realiz en bicicleta por Catalua en el verano de
            2001. Durante ese periodo, descubri que las opciones tursticas que
            ofrecan ciudades como Barcelona, Girona y Reus dependan de
            transportes contaminantes y actividades poco respetuosas con la
            naturaleza y el entorno, algo que causaba un progresivo deterioro de
            los increbles parajes naturales de Catalua. <br />
            Preocupada por la falta de alternativas eco-friendly, Marina se
            propuso fundar una agencia de experiencias tursticas donde el
            respeto por la naturaleza fuera la principal prioridad.
            <br />
            As naci Econotravel Barcelona.
            <br />
            <br />
            <Box
              style={{
                boxShadow: "#2e5137 0px 2px 8px",
                backgroundColor: "#b5bea7",
                borderRadius: "50px",
                textAlign: "center",
                fontStyle: "italic",
                marginTop: "3rem",
                padding: "0.5rem",
              }}
            >
              Telfono: 93 123 45 67
              <br />
              Correo: info@econotravel.com <br />
              Direccin: Carrer dels Viatgers, 28, 08001 Barcelona
            </Box>
          </Typography>
        </Grid>
      </Grid>
    </section>
  );
}
