import React, { useState } from "react";
import "../App.css";
import { Typography } from "@mui/material"; 




const LeerMs = ({ children }) => {
  const text = children;
  const [isLeerMs, setIsLeerMs] = useState(true);
  const toggleLeerMs = () => {
    setIsLeerMs(!isLeerMs);
  };
  return (
    <p className="text">
      {isLeerMs ? text.slice(0, 200) : text}
      <span onClick={toggleLeerMs} className="read-or-hide">
        {isLeerMs ? "   ...LeerMs" : "   ...MostrarMenos"}
      </span>
    </p>
  );
};
  
const Content = () => {
  return (
    <div className="container">
       <Typography
            variant="body1"
            color= "#2e5137"
            style={{ marginLeft:"2em", fontSize: "1.2rem", fontWeight: "400" }}>  "Disfruta de un hermoso paseo en bicicleta por el increble Parque Natural del Montseny. 
           
             
        <LeerMs>
        "Disfruta de un hermoso paseo en bicicleta por el increble Parque Natural del Montseny. 
        Una escapada veraniega perfecta para parejas, familias y amigos que nos permitir conocer nuevos y sorprendentes lugares.
        El recorrido que os proponemos comienza con una larga subida con algunos descansos, combinando asfalto y pistas anchas,
        culminando en un mirador con magnficas vistas del Valls y el mar. 
        Desde aqu continuaremos la bajada combinando senderos, pistas y algn tramo de carretera y terreno mixto para visitar la zona de Santa Fe, 
        donde descubriremos la Ermita y el Bosque de las Secuoyas. 
        Para finalizar, acudiremos al restaurante Mara Rosa de Palautordera, donde disfrutaremos de una excelente comida casera con butifarra blanca y negra y mongetes del Montseny."
        </LeerMs>
     </Typography>
    </div>
  );
};
  
export default Content;